.modal__container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80%;
    max-width: 1024px;
    margin: auto;
    position: fixed;
    height: 600px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 2px solid orange;
    background-color: black;
    /* overflow: auto; */
}
.wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(3px);
}
.modal__item{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    align-items: flex-start;
    margin: var(--spacing-md);
    border: 4px solid var(--color-light);
    box-shadow: 0 2px 24px rgba(0,0,0,0.4);
    background-color: var(--color-light);
    padding: 15px;
}
.modal__img{
    width: 50%;
    height: 100%;
    object-fit: contain;
}
.modal__name{
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    /* white-space: nowrap; */
    border: 1px solid black;
    text-align: center;
    color: white;
    font-size: 30px;
}
.modal__name-span{
    margin-top: 10px;
    font-size: 14px;
    white-space: nowrap;
}
.modal__close{
    position: fixed;
    top: 20px;
    right: 20px;
    font-size: 24px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
.modal__not{
    position: fixed;
    bottom: 20px;
    left: 20px;
    width: 250px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-light);
    border-radius: 4px;
}
.modal__content{
    width: 50%;
    display: flex;
    flex-direction: column;
}
.modal__list-item{
    color: #fff;
    text-transform: capitalize;
}
.modal__list{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 30px;
}